button {
  width: 20vw;
  height: 20vw;
  padding: 15px 0;
  margin-left: 10vw;
  margin-right: 10vw;
  border: none;
  background: var(--color);
  border-radius: 10vw;
  color: white;
  font-size: 42px;
  line-height: 24px;
  cursor: pointer;
  user-select: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}
button:hover {
  filter: brightness(85%);
}
button:active {
  filter: brightness(60%);
}

